<style scoped>
  .form-wrap {
    max-height: 260px;
    overflow-y: auto;
  }
  .filter-goods-name-wrap .el-tag {
    margin-right: 10px;
  }
  .filter-goods-name-wrap .button-new-tag {
    margin-right: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .filter-goods-name-wrap .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>
<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" size="mini" icon="el-icon-plus" @click="addScreenSet">新建模板</el-button>
        <el-button type="primary" size="mini" icon="el-icon-setting" @click="showSetDialog" v-if="cityOptions.length > 0">商品名称过滤配置</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="模板类型">
            <el-select class="search-input" v-model="searchContent.templateType" placeholder="请选择模板类型" size="small"
              @change="doSearchHandel(true)" clearable>
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in templateTypeOptions">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="区域">
            <el-select class="search-input" v-model="searchContent.cityId" placeholder="请选择区域" size="small"
              @change="cityChange" filterable clearable>
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in cityOptions">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="门店">
            <el-select class="search-input" v-model="searchContent.storeNo" placeholder="请选择门店" size="small" filterable
              clearable @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in storeOptions">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="状态">
            <el-select class="search-input" v-model="searchContent.enable" placeholder="状态" size="small" clearable
              @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in enableOptions">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="模板名称">
            <el-input v-model="searchContent.templateName" size="small" class="search-input" placeholder="模板名称"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="table-wraper">
      <el-table class="fixed-height" v-loading="loading" :data="tableData" empty-text="没有查询到您想要的数据" style="width:100%;"
        size="small">
        <el-table-column prop="id" width="60" label="ID"></el-table-column>
        <el-table-column min-width="120px" prop="templateName" label="模板名称"></el-table-column>
        <el-table-column min-width="100px" prop="templateType" label="模板类型" :formatter="templateTypeFormatter">
        </el-table-column>
        <el-table-column min-width="100px" prop="display" label="显示方式" :formatter="displayFormatter"></el-table-column>
        <el-table-column min-width="120px" label="区域/门店">
          <template slot-scope="scope" size="small">
            <div v-if="scope.row.templateType === 1">{{scope.row.cityName}}</div>
            <div v-else>{{scope.row.cityName}}/{{scope.row.storeName}}</div>
          </template>
        </el-table-column>
        <el-table-column min-width="80px" prop="enable" label="状态" :formatter="enableFormatter"></el-table-column>
        <el-table-column min-width="100px" prop="createSysUserName" label="创建人"></el-table-column>
        <el-table-column min-width="170px" prop="createTime" label="创建时间" :formatter="timeFormatter"></el-table-column>
        <el-table-column fixed="right" label="操作" width="140">
          <template slot-scope="scope" size="small">
            <div>
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete"
                v-if="scope.row.enable === 1">删除</el-button>
              <el-button @click="operateHandel(scope.row)" type="text" size="mini"
                :icon="scope.row.enable === 0 ? 'el-icon-error' : 'el-icon-success'">
                {{scope.row.enable === 0 ? '禁用' : '启用'}}</el-button>
            </div>
            <div>
              <el-button @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
              <!-- <el-button @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-view">预览</el-button> -->
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination v-if="total>0" class="tc mt20" background @size-change="changeSize" @current-change="changeCurrent"
        :current-page.sync="pageNum" :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
    <div>
      <el-dialog title="商品名称过虑配置" :visible.sync="goodsSetVisible" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false" width="520px">
        <el-form ref="form" label-width="80px" v-loading="formLoading">
          <div class="form-wrap">
            <el-form-item label="配置区域">
              <el-select class="dialog-input" v-model="cityId" placeholder="配置区域" size="small" @change="areaCityChange" filterable clearable>
                <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in cityOptions">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="过滤内容" class="filter-goods-name-wrap">
              <el-tag
                :key="index"
                v-for="(tag, index) in dynamicTags"
                closable
                :disable-transitions="false"
                @close="handleClose(tag)">
                {{tag}}
              </el-tag>
              <el-input
                class="input-new-tag"
                v-if="inputVisible"
                v-model="inputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleInputConfirm"
                @blur="handleInputConfirm"
              >
              </el-input>
              <el-button v-else class="button-new-tag" size="small" @click="showInput">+ 添加内容</el-button>
            </el-form-item>
          </div>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="cancelHandel">取消</el-button>
          <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 查询用户区域店组一级列表
    userAreaStoreGroupList: '/foundation/store/userStoreCityList',
    // 查询用户权限分店列表
    userStoreList: '/foundation/store/userStoreList',
    getList: '/plateform/p/tv/template/getList',
    // 删除
    delete: '/plateform/p/tv/template/delete',
    // 详情
    detail: '/plateform/p/tv/template/detail',
    // 禁用启用
    operate: '/plateform/p/tv/template/operate',
    // 获取商品名过滤字段
    getGoodsFilterKey: '/plateform/p/tv/template/getGoodsFilterKey',
    // 设置商品名过滤字段
    setGoodsFilterKey: '/plateform/p/tv/template/setGoodsFilterKey'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        tableData: [],
        searchContent: {
          DIFFCITY: true,
          templateType: '', // 模板类型 1：区域模板 2：门店模板
          templateName: '', // 模板名称
          cityId: '', // 城市id（区域id）
          storeNo: '', // 门店编码
          enable: '' // 0启用，1禁用
        },
        templateTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '区域模板',
            value: 1
          },
          {
            label: '门店模板',
            value: 2
          }
        ],
        enableOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '已启用',
            value: 0
          },
          {
            label: '已禁用',
            value: 1
          }
        ],
        cityOptions: [],
        storeOptions: [],
        goodsSetVisible: false,
        formLoading: false,
        
        // 添加过滤内容
        dynamicTags: [],
        cityId: '',
        inputVisible: false,
        inputValue: ''
      }
    },
    created() {
      let searchContentMsg = Storage.get("searchContent")
      if (this.$route.meta.keepAlive && searchContentMsg) {
        this.pageNum = searchContentMsg.pageNum
        this.searchContent = searchContentMsg.searchContent
      }
      this.doSearchHandel()
      Storage.remove('searchContent')
      this.getCityList()
    },
    methods: {
      templateTypeFormatter(row, column, cellValue) {
        if (cellValue === 1) {
          return '区域模板'
        } else if (cellValue === 2) {
          return '门店模板'
        } else {
          return '--'
        }
      },
      displayFormatter(row, column, cellValue) {
        if (cellValue === 0) {
          return '横屏显示'
        } else if (cellValue === 1) {
          return '竖屏显示'
        } else {
          return '--'
        }
      },
      enableFormatter(row, column, cellValue) {
        if (cellValue === 0) {
          return '已启用'
        } else if (cellValue === 1) {
          return '已禁用'
        } else {
          return '--'
        }
      },
      timeFormatter(row, column, cellValue) {
        return cellValue ? utils.timestampToTime(cellValue) : ''
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent(value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      getCityList() {
        this.$http.get(reqUrl.userAreaStoreGroupList).then(res => {
          if (res.code === 0) {
            let cityList = res.data && res.data.length ? res.data : []
            this.cityOptions = cityList.map(item => {
              let newItem = {
                label: `${item.cityId}_${item.cityName}`,
                value: item.cityId
              }
              return newItem
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示【获取区域列表】：", e.message)
        })
      },
      cityChange(e) {
        let _params = {
          DIFFCITY: true,
          pageNum: 1,
          pageSize: -1,
          cityId: e
        }
        this.searchContent.storeNo = ""
        this.$http.post(reqUrl.userStoreList, {
          ..._params
        }).then(res => {
          if (res.code === 0) {
            let storeList = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.storeOptions = storeList.map(item => {
              let newItem = {
                label: `${item.storeNo}_${item.storeName}`,
                value: item.storeNo
              }
              return newItem
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
        this.doSearchHandel(true)
      },
      addScreenSet() {
        this.$router.push("/sc861816/add")
      },
      doSearchHandel(_param) {
        if (_param === true) {
          this.pageNum = 1
        }
        this.loading = true
        this.$http.post(reqUrl.getList, {
          ...this.searchContent,
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }).then(res => {
          this.loading = false
          if (res.code === 0) {
            // 获取表格数据
            this.tableData = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.total = res.data.total
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      deleteItem(_row) {
        let _params = {
          DIFFCITY: true,
          id: _row.id
        }
        this.$confirm('确定要删除吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(reqUrl.delete, {
            ..._params
          }).then(res => {
            if (res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {});
      },
      operateHandel(_row) {
        let _params = {
          DIFFCITY: true,
          id: _row.id
        }
        let tipTxt = ''
        if (_row.enable === 0) {
          tipTxt = "确定要禁用吗?"
          _params.enable = 1
        } else if (_row.enable === 1) {
          tipTxt = "一个门店或区域有且仅有一个可生效模板，开启后原模板将被禁用，确定要启用吗?"
          _params.enable = 0
        }
        this.$confirm(tipTxt, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(reqUrl.operate, {
            ..._params
          }).then(res => {
            if (res.code === 0) {
              if (_row.enable === 0) {
                this.$message.success("禁用成功")
              } else if (_row.enable === 1) {
                this.$message.success("启用成功")
              }
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      editItem(_row) {
        // 设置搜索条件
        let _paramMsg = {
          searchContent: this.searchContent,
          pageNum: this.pageNum
        }
        Storage.set("searchContent", _paramMsg)
        this.$router.push(`/sc861816/edit/${_row.id}`)
      },
      /**
       * @description: 打开设置的dialog
       * @param {type} 
       * @return: 
       */
      showSetDialog() {
        let cityId = Storage.get("cityId") || ''
        this.cityId = cityId
        this.dynamicTags = ''
        // TODO 拿cityId 获取区域配置的过滤内容
        this.getFilterMsg(cityId)
        this.goodsSetVisible = true
      },
      /**
       * @description: 配置区域发生变化
       * @param {type} 
       * @return: 
       */
      areaCityChange(e) {
        // TODO 调用获取配置详情的接口
        this.getFilterMsg(e)
      },

      handleClose(tag) {
        this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
      },

      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },

      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.dynamicTags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },

      /**
       * @description: 获取返回的过滤内容
       * @param {type} 
       * @return: 
       */
      getFilterMsg(_cityId) {
        this.formLoading = true
        this.$http.post(reqUrl.getGoodsFilterKey, {DIFFCITY: true, cityId: _cityId}).then(res => {
          if (res.code === 0) {
            this.formLoading = false
            let filterStrList = res.data || []
            this.dynamicTags = filterStrList
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.formLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 关闭弹窗
       * @param {type} 
       * @return: 
       */
      cancelHandel() {
        this.$confirm('取消将不保存编辑的任何内容，确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.goodsSetVisible = false
        }).catch(() => {})
      },
      /**
       * @description: 保存设置
       * @param {type} 
       * @return: 
       */
      doSaveHandel() {
        this.isDisabled = true
        this.$http.post(reqUrl.setGoodsFilterKey, {DIFFCITY: true, cityId: this.cityId, keyList: this.dynamicTags}).then(res => {
          if (res.code === 0) {
            setTimeout(() => {
              this.isDisabled = false
            }, 500);
            this.$message.success("保存成功")
            this.goodsSetVisible = false
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          setTimeout(() => {
            this.isDisabled = false
          }, 500);
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    }
    
  }

</script>



